import React from 'react'
import './Navbar.css'
import logo from "./pictures/logo.svg"
import  { useState } from 'react';
import {Squash as Hamburger } from 'hamburger-react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  
function Navbar(){
    const [state, setstate] = useState("open nav")
    function reverse(){
        if (state=="close nav"){
            setstate("open nav")
        }else{
            setstate("close nav")
        }
    } 
    return (
        <div class="navbar">
            <div class="wrapper-navbar">
                <div class="logo-navbar"><img src={logo}></img></div>
                <button onClick={() => reverse()} class="hamburger"><Hamburger/></button>
                <div class="navbar-desktop">
                    <ul>
                    <Link to="/">Home</Link>
                    <Link to="/blog">Blog</Link>
                    <Link to="/gallery">Gallery</Link>
                    <Link to="/members">Members</Link>
                    </ul>
                </div>
            </div>
                <div class={state}>
                    <ul>
                    <Link to="/">Home</Link>
                    <Link to="/blog">Blog</Link>
                    <Link to="/gallery">Gallery</Link>
                    <Link to="/members">Members</Link>
                    </ul>
                </div>
        </div>
    )
}

export default Navbar
